<template>
  <div class="record__content">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm
        :formConfig="formConfig"
        ref="editForm"
        :formData="editFormData"
      >
        <template #assetsNumber="{editFormInfo}">
          <el-input
            placeholder="请选择资产信息"
            v-model="editFormInfo.assetsNumber"
            readonly
            :disabled="pageType == 'details' && !isWorkFlowEdit"
            size="mini"
            class="input-with-select"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click.stop="goSeleteAsset"
            ></el-button>
          </el-input>
        </template>
      </editForm>
    </div>
    <div
      class="process_score"
      v-if="editFormData.status && editFormData.status == 1"
    >
      <dividerTitle :title="'评分'"></dividerTitle>
      <processScore
        v-if="showScore"
        :scoreSetting="scoreSetting"
        :hideBtn="scoreHideBtn"
        :showScore.sync="showScore"
      ></processScore>
    </div>
    <div class="record__attach">
      <dividerTitle :title="'附件'"></dividerTitle>
      <attachTable
        :attachSetting="attachSetting"
        :hideBtn="hideBtn"
        ref="attachFile"
      ></attachTable>
    </div>

    <div
      class="record__workflow"
      v-if="
        pageType == 'details' &&
          editFormData.status &&
          editFormData.status !== 2
      "
    >
      <dividerTitle :title="'工作流'"></dividerTitle>
      <workFlow
        :currentFlowData="editFormData"
        :beforeSubmit="beforeSubmit"
        :otherParams="workFlowParams"
        @submitSuccess="workFlowSuccess"
      ></workFlow>
    </div>
    <assetSelete
      v-if="showSelete"
      :hadSeleteData="hadSeleteData"
      @changeSelete="changeSeleteAsset"
      :showSelete.sync="showSelete"
      :userID="employeeId"
    ></assetSelete>
  </div>
</template>

<script>
import {
  getDetailsApi, //获取详情
  updateApi
} from "@/api/itService/informationResourceApply";
export default {
  props: {
    formData: {
      type: Object,
      default: new Object()
    },
    pageType: {
      type: String,
      default: ""
    },
    defaultSet: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // userInfo: this.$store.getters.userInfo,
      hadSeleteData: [],
      employeeId: null,
      showSelete: false,
      editFormData: { suitableWorkArr: [] },
      trainingType: {},
      formConfig: {
        itemArr: [
          {
            label: "申请日期",
            prop: "billDate",
            type: "date",
            // required: true,
            span: 8
          },
          {
            label: "申请人",
            prop: "staffunName",
            idsStr: "employeeId",
            type: "user",
            trigger: "change",
            // required: true,
            methods: obj => {
              let { orgNodeName, orgNodeCode, mobile, id } = obj[0];
              this.employeeId = id;
              this.$refs.editForm.setFormItem("depatName", orgNodeName);
              this.$refs.editForm.setFormItem("depatCode", orgNodeCode);
              this.$refs.editForm.setFormItem("phone", mobile);
            }
          },
          {
            label: "申请部门",
            prop: "depatName",
            id: "depatCode",
            // required: true,
            disabled: true,
            placeholder: "选择申请人后自动带出",
            type: "text"
          },
          {
            label: "联系号码",
            prop: "phone",
            // required: true,
            type: "text",
            placeholder: "选择申请人后自动带出",
            rules: {
              validator: function(rule, value, callback) {
                const reg = /^[0-9]*$/;
                if (!value) {
                  // return callback(new Error("请填写手机号码！"));
                } else if (!reg.test(value)) {
                  return callback(new Error("请填写正确的手机号码！"));
                } else {
                  callback();
                }
              },
              trigger: "change"
            }
          },
          {
            label: "资产编号",
            prop: "assetsNumber",
            type: "text",
            // required: true
          },
          {
            type: "text",
            label: "资产类型",
            prop: "assetTypeName",
            // idsStr:"assetTypeId",
            placeholder: "选择资产后自动带出",
            disabled: true
          },
          {
            label: "资产型号",
            prop: "goodsModel",
            type: "text",
            placeholder: "选择资产后自动带出",
            disabled: true
          },
          {
            type: "select",
            label: "业务类型",
            prop: "apptype",
            // required: true,
            seleteOption: this.$store.getters["dirtData"]("msg_type"),
            formatter: val => {
              return this.$store.getters["fineDirtDataName"]("msg_type", val);
            }
          },
          {
            label: "审批人",
            prop: "confirmManName",
            idsStr: "confirmMan",
            type: "user",
            isHide: this.pageType == "details" && !this.isWorkFlowEdit,
            // required: true,
            trigger: "change"
          },
          {
            label: "申请描述",
            prop: "appContent",
            type: "textarea",
            span: 24,
            rows: 4
          },
          {
            label: "备注",
            prop: "remark",
            type: "textarea",
            span: 24,
            rows: 4
          }
        ]
      },
      procInstType:null,
      //附件设置
      attachSetting: {
        title: "",
        select: false,
        billType: this.procInstType, //页面标识
        sourceId: "", //单据的ID
        btnArr: ["add", "delete", "save"]
      },
      //评分设置
      scoreSetting: {
        title: "",
        billType: "asset_regist", //页面标识
        sourceId: "", //单据的ID
        data: [],
        btnArr: ["save"]
      },
      scoreHideBtn: false,
      coverList: [],
      dialogVisible: false,
      activeName: "0",
      hideBtn: false,
      showScore: false,
      beforeSubmit: {
        //工作流提交前检测
        methods: data => {
          return this.submitBefore(data);
        }
      }
    };
  },
  computed: {
    //工作流额外参数
    workFlowParams() {
      let { procInstType } = this.editFormData;
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return {
          procInstType: procInstType || "",
          assignercodes: [this.editFormData.confirmMan]
        };
      }
      return { procInstType: procInstType || "" };
    },
    //是否是流程中可编辑
    isWorkFlowEdit() {
      if (this.formData.status === 4 && this.formData.workflowIsApprover) {
        return true;
      } else {
        return false;
      }
    }
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      //row.status
      if (this.pageType == "add") {
        let defaultForm = {};
        if (this.defaultSet) {
          defaultForm = { ...this.formData };
        }
        this.editFormData = Object.assign({}, defaultForm, {
          billDate: this.$utils.getNowDay()
        });
      } else {
        this.getDetails(this.formData.id);
      }
      if (this.pageType == "details") {
        if (!this.isWorkFlowEdit) {
          this.formConfig.itemArr.map(i => (i.disabled = true));
        }
        this.attachSetting.sourceId = this.formData.id; //附件id
      }
      if (this.pageType == "score") {
        //评分
        this.scoreSetting.sourceId = this.formData.id;
        this.formConfig.itemArr.map(i => (i.disabled = true));
        this.showScore = true; //显示评分
      }
      // console.log(this.$store.getters.userInfo);
    },

    //获取详情
    async getDetails(id) {
      let res = await getDetailsApi(id);
      let obj = res.data || {};
      // console.log(res.data );
      this.procInstType = obj.procInstType;
      this.attachSetting.sourceId = obj.id;
      this.editFormData = Object.assign({}, obj, {
        confirmMan: obj.empId,
        confirmManName: obj.zystaffunName
      });
    },
        //提交前检测
    submitBefore(data) {
      //如果是起草节点
      if (this.isWorkFlowEdit) {
        return new Promise((resolve, reject) => {
          this.$refs.editForm
            .getFormData()
            .then((data) => {
              let {
                billDate,
                employeeId,
                depatcode,
                phone,
                assetsNumber,
                assetTypeName,
                goodsModel,
                apptype,
                confirmMan,
                appContent,
                remark,
                sourceId,
                id,
              } = data;
              let params = {
                billDate,
                employeeId,
                depatcode,
                phone,
                assetsNumber,
                assetTypeName,
                goodsModel,
                apptype,
                confirmMan,
                appContent,
                remark,
                sourceId,
                submitType: 1,
                id,
              };
              this.editFormData = Object.assign({}, data);
              
              updateApi(params)
                .then((res) => {
                  resolve();
                })
                .catch((e) => {
                  reject(e);
                });
            })
            .catch((e) => {
              reject("表单信息未填写完整");
            });
        });
      } else {
        return true;
      }
    },
    //工作流提交成功之后
    workFlowSuccess() {
      this.$emit("update:pageType", "list");
    },
    //获取表单数据
    getFormData() {
      return this.$refs.editForm.getFormData();
    },
    //点击选择资产
    goSeleteAsset() {
      // console.log("diaoyong");
      if (this.employeeId) {
        if (this.pageType == "details") {
          return;
        }
        let sourceId = this.$refs.editForm.getFormItem("sourceId");
        this.hadSeleteData = [{ id: sourceId }];

        this.showSelete = true;
      } else {
        this.$message.warning("请先选择申请人");
      }
    },
    //确定选择资产
    changeSeleteAsset(list) {
      // console.log(list);
      let [obj] = list;
      let {
        assetsNumber,
        assetTypeName,
        assetTypeId,
        goodsModel,
        id: sourceId
      } = obj; //assetsNumber
      this.$refs.editForm.setFormItem("assetsNumber", assetsNumber);
      this.$refs.editForm.setFormItem("assetTypeName", assetTypeName);
      // this.$refs.editForm.setFormItem("assetTypeId", assetTypeId);
      this.$refs.editForm.setFormItem("goodsModel", goodsModel);
      this.$refs.editForm.setFormItem("sourceId", sourceId);
    }
  }
};
</script>

<style scoped lang="scss">
</style>