import request from '@/request/api'
/**===========基本信息接口 */
//获取列表
export function getListApi(data = {}) {
    return request({
        url: '/workorder/msgappAction/queryList.htm',
        method: 'post',
        data: data
    })
}
//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/workorder/msgappAction/detail.htm',
        method: 'post',
        params:{ id: id }
    })
}
//删除
export function deleteApi(id = "") {
    return request({
        url: '/workorder/msgappAction/delete.htm',
        method: 'post',
        params: { id: id }
    })
}

//提交流程
export function addApi(data = {}) {
    return request({
        url: '/workorder/msgappAction/addMsgapp.htm',
        method: 'post',
        data: data
    })
}
//保存草稿
export function updateApi(data = {}) {
    return request({
        url: '/workorder/msgappAction/updateMsgapp.htm',
        method: 'post',
        data: data
    })
}


